@import "../scss/variables";

button.button,
a.button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: 700;
  text-decoration: none;
  border: 2px solid transparent;
  cursor: pointer;
  border-radius: 25px;
  background-color: $dark-blue;
  color: white;
  transition: background-color 0.2s;
  outline: 0;

  &:focus,
  &:active {
    border: 2px solid $light-blue;
  }

  &:hover {
    background-color: $navy-blue;
  }

  &.active {
    background-color: $navy-blue;
  }
}

.btn-quiz-link {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 5;

  svg {
    width: 18px;
    height: 18px;
    fill: white;
    margin-left: 0.5rem;
  }
}

.btn-answer {
  display: flex;
  width: 100%;
  padding: 1rem;
  padding-left: 0;
  border-radius: 25px;
  outline: 0;
  border: 0;
  margin-bottom: 2rem;
  cursor: pointer;
  font-size: 0.9rem;
  border: 2px solid transparent;
  background-color: #f0f0f0;

  @include md {
    font-size: 1.6rem;
  }

  &:focus,
  &:active {
    border: 2px solid $light-blue;
  }

  &__number {
    padding: 0 1rem;
  }

  &.correct {
    background-color: $correct;

    &:focus,
    &:active {
      border: 2px solid $correct;
    }
  }

  &.incorrect {
    background-color: $incorrect;

    &:focus,
    &:active {
      border: 2px solid $incorrect;
    }
  }
}
