@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;700&family=Kaushan+Script&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Montserrat", sans-serif}button.button,a.button{padding:0.5rem 1rem;font-size:1rem;font-weight:700;text-decoration:none;border:2px solid transparent;cursor:pointer;border-radius:25px;background-color:#023e8a;color:white;transition:background-color 0.2s;outline:0}button.button:focus,button.button:active,a.button:focus,a.button:active{border:2px solid #0077b6}button.button:hover,a.button:hover{background-color:#03045e}button.button.active,a.button.active{background-color:#03045e}.btn-quiz-link{position:fixed;bottom:2rem;right:2rem;z-index:5}.btn-quiz-link svg{width:18px;height:18px;fill:white;margin-left:0.5rem}.btn-answer{display:-webkit-flex;display:flex;width:100%;padding:1rem;padding-left:0;border-radius:25px;outline:0;border:0;margin-bottom:2rem;cursor:pointer;font-size:0.9rem;border:2px solid transparent;background-color:#f0f0f0}@media (min-width: 600px){.btn-answer{font-size:1.6rem}}.btn-answer:focus,.btn-answer:active{border:2px solid #0077b6}.btn-answer__number{padding:0 1rem}.btn-answer.correct{background-color:#4bb543}.btn-answer.correct:focus,.btn-answer.correct:active{border:2px solid #4bb543}.btn-answer.incorrect{background-color:#ff1f1f}.btn-answer.incorrect:focus,.btn-answer.incorrect:active{border:2px solid #ff1f1f}.max-width{max-width:100%;margin:0 auto;padding:2rem}@media (min-width: 600px){.max-width{max-width:600px}}@media (min-width: 1200px){.max-width{max-width:935px}}@media (min-width: 600px){.max-width{padding:2rem 0}}body.ie11 .card-container__grid{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}body.ie11 .card-container__grid .card{width:calc(50% - 1rem);margin-bottom:2rem}body.ie11 .card-container__grid .card:nth-of-type(odd){margin-right:1rem}body.ie11 .card-container__grid .card:nth-of-type(even){margin-left:1rem}body.ie11 .card-container__grid .card:last-of-type{margin-bottom:0}

.main-nav{padding:1rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#023e8a}.main-nav a{color:white;font-size:1.4rem;font-weight:700;text-decoration:none;font-family:"Kaushan Script",cursive}.main-nav a:hover{text-decoration:underline}

.intro-text{margin-bottom:2rem}.intro-text h1{font-family:"Kaushan Script",cursive;margin-bottom:1rem}.intro-text p{margin-bottom:1rem}.intro-text ul li{list-style-position:inside;margin-bottom:0.25rem}

.view-buttons{position:fixed;bottom:2rem;left:2rem;z-index:5;display:none}@media (min-width: 600px){.view-buttons{display:block}}.view-buttons button:first-of-type{border-top-right-radius:0px;border-bottom-right-radius:0px}.view-buttons button:last-of-type{border-top-left-radius:0px;border-bottom-left-radius:0px}.view-buttons button svg{width:12px;height:12px;fill:white;margin-left:0.5rem}

.card-container__grid{display:block}@media (min-width: 600px){.card-container__grid{display:grid;grid-template-columns:1fr 1fr;grid-gap:2rem;gap:2rem}}.card-container__list{display:block}

.card{box-shadow:1px 2px 8px 3px rgba(0,0,0,0.3);border-radius:10px;padding:1.5rem;margin-bottom:2rem}@media (min-width: 600px){.card{margin-bottom:0}}.card__number{width:50px;height:50px;position:absolute;top:0.75rem;left:0.75rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;border-radius:50%;background-color:#023e8a;color:white;font-size:1.4rem;font-weight:700;z-index:2}.card__image{z-index:1;position:relative}.card__image img{max-width:100%;max-height:100%;border-radius:10px}.card__content .heading{margin-bottom:1rem;font-family:"Kaushan Script",cursive}.card__content .post-date{font-size:0.8rem;margin-bottom:1rem;font-style:italic;color:#023e8a}.card-container__grid .card{position:relative}.card-container__grid .card figure img{height:100%;width:100%;object-fit:cover}.card-container__grid .card__content{padding:1rem 0;font-size:1rem}.card-container__list .card{position:relative;display:-webkit-flex;display:flex;margin-bottom:2rem}.card-container__list .card__image{-webkit-flex:3 1;flex:3 1}.card-container__list .card__content{-webkit-flex:2 1;flex:2 1;padding:1rem;height:25%;background-color:#fff;font-size:1rem}

.loader{text-align:center;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.loader>div{width:30px;height:30px;background-color:#03045e;border-radius:100%;display:inline-block;-webkit-animation:sk-bouncedelay 1.4s infinite ease-in-out both;animation:sk-bouncedelay 1.4s infinite ease-in-out both}.loader .bounce1{-webkit-animation-delay:-0.32s;animation-delay:-0.32s}.loader .bounce2{-webkit-animation-delay:-0.16s;animation-delay:-0.16s}@-webkit-keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}@keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0);transform:scale(0)}40%{-webkit-transform:scale(1);transform:scale(1)}}

.score{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center}.score h1{font-size:2rem;margin-bottom:1rem}.score p{margin-bottom:1rem}

.sr-only{visibility:hidden;height:0}.questions{margin-bottom:2rem}.questions h2{margin-bottom:1rem}.questions p{line-height:1.4}

